import React, { useState } from "react";
import { useForm, SubmitHandler } from "react-hook-form";
import InputMask from "react-input-mask";
import Alert from "@mui/material/Alert";
import axios from "axios";
import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import luhnCheck from "../utils/luhnCheck";
import { Card, ModalAddCardProprs } from "../models/interfaces";

export default function ModalAddCard({
  handleCloseAddCard,
  listCurrency,
  setUpdateForm,
  openAddCard,
}: ModalAddCardProprs) {
  const [cardValidationError, setCardValidationError] = useState(false);
  const [cardExists, setCardExists] = useState(false);

  const {
    register,
    formState: { errors },
    handleSubmit,
  } = useForm<Card>();

  const onSubmit: SubmitHandler<Card> = (data) => {
    if (cardValidationError === true) return null;
    axios
      .post("api/wallet/card/add", { ...data })
      .then((res) => {
        if (res.data === "Card already exist") setCardExists(true);
        if (res.data === "Card added") {
          handleCloseAddCard();
          setUpdateForm((prev) => !prev);
        }
      })
      .catch((err) => console.log(err));
  };

  const handleChangeCard = (e: React.ChangeEvent<HTMLInputElement>): void => {
    let card = e.target.value;
    card = card.replace(/\D+/g, "");

    // // Card validation via binlist API
    // if (cardValidationError === false) {
    //     if (card.length === 16) {
    //         axios
    //             .get(`https://lookup.binlist.net/${card}`)
    //             .then()
    //             .catch(() => setCardValidationError(true))
    //     } else setCardValidationError(false)
    // } else if (card.length < 16) {
    //     setCardValidationError(false)
    // } else return;

    // Card validation via Luhn`s algorithm
    if (cardValidationError === false) {
      if (card.length === 16) {
        if (luhnCheck(card) === false) {
          setCardValidationError(true);
        }
      } else setCardValidationError(false);
    } else if (card.length === 15) {
      setCardValidationError(false);
      setCardExists(false);
    } else return;
  };

  return (
    <>
      <Modal open={openAddCard} onClose={handleCloseAddCard}>
        <Box className='modalBox'>
          {cardValidationError ? (
            <Alert severity='error'>
                   ,  
            </Alert>
          ) : errors.pan ? (
            <Alert severity='error'>
              {errors.pan.message}
            </Alert>
          ) : errors.expire_date ? (
            <Alert severity='error'>
              {errors.expire_date.message}
            </Alert>
          ) : errors.cvv ? (
            <Alert severity='error'>
              {errors.cvv.message}
            </Alert>
          ) : errors.card_holder ? (
            <Alert severity='error'>
              {errors.card_holder.message}
            </Alert>
          ) : (
            errors.amount && (
              <Alert severity='error'>
                {errors.amount.message}
              </Alert>
            )
          )}
          {cardExists && <Alert severity='error'>   !</Alert>}

          <form onSubmit={handleSubmit(onSubmit)} className='modalWindow'>
            <div className='form-group'>
              <label> </label>
              <InputMask
                {...register("pan", {
                  required: "   ",
                  pattern: {
                    value: /[0-9]{4}[ ][0-9]{4}[ ][0-9]{4}[ ][0-9]{4}/,
                    message: "     16 ",
                  },
                })}
                onChange={handleChangeCard}
                className='form-control'
                mask='9999 **** **** 9999'
                placeholder='0000 0000 0000 0000'
                required
              />
            </div>

            <div className='modalSmallInputGroup'>
              <div>
                <label> </label>
                <InputMask
                  {...register("expire_date", {
                    required: "    ",
                    pattern: {
                      value: /(?:(?:0[1-9]|1[0-2]))[/]((2[2-9])|([3-9][0-9]))/,
                      message: "   ",
                    },
                  })}
                  className='form-control modalSmallInput'
                  mask='99/99'
                  placeholder='00/00'
                  required
                />
              </div>
              <div>
                <label>CVV</label>
                <InputMask
                  {...register("cvv", {
                    required: "  CVV- ",
                    pattern: {
                      value: /[0-9]{3}/,
                      message: "CVV-   ",
                    },
                  })}
                  className='form-control modalSmallInput'
                  mask='999'
                  placeholder='000'
                  required
                />
              </div>
            </div>

            <div className='form-group'>
              <label> </label>
              <input
                {...register("card_holder", {
                  pattern: {
                    value: /^[a-zA-Z]+\s[a-zA-Z]+$/,
                    message: "'  ",
                  },
                })}
                className='form-control'
                onKeyPress={(event) => {
                  if (!/[a-zA-Z]|[ ]/.test(event.key)) event.preventDefault();
                }}
                onChange={(event) => {
                  event.target.value = event.target.value.toUpperCase();
                }}
              />
            </div>

            <div className='form-group'>
              <label> </label>
              <input
                {...register("name")}
                className='form-control'
                maxLength={20}
              />
            </div>

            <div className='modalSmallInputGroup'>
              <div>
                <label></label>
                <input
                  {...register("amount", {
                    pattern: {
                      value: /[0-9]/,
                      message: "  ",
                    },
                    value: 0,
                  })}
                  placeholder='0'
                  className='input form-control'
                  onKeyPress={(event) => {
                    if (!/[0-9]/.test(event.key)) event.preventDefault();
                  }}
                />
              </div>
              <div>
                <label></label>
                <select {...register("currencyName")} className='form-control'>
                  {listCurrency.map((currency: string, index: number) => (
                    <option key={index} value={currency}>
                      {currency}
                    </option>
                  ))}
                </select>
              </div>
            </div>

            <div className='modalButtonGroup'>
              <input
                type='submit'
                value=''
                className='btn btn-primary modalButton'
              />
              <input
                type='button'
                value=''
                onClick={handleCloseAddCard}
                className='btn btn-primary modalButton'
              />
            </div>
          </form>
        </Box>
      </Modal>
    </>
  );
}
